import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { JhiEventManager } from 'ng-jhipster';
import { OrganisationService } from './organisation.service';

declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./organisation-manage.component.html",
    providers: [OrganisationService]
})
export class OrganisationDeleteComponent implements OnInit {
    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        protected eventManager: JhiEventManager,
        public organisationService: OrganisationService,
        protected pluginService: PluginService
    ) { }

    ngOnInit() {
        this.activatedRoute.data.subscribe(({ organisation }) => {
            this.deleteOrganisation(organisation);
        });
    }

    protected subscribeToDeleteResponse(resp: Observable<HttpResponse<any>>) {
        resp.subscribe((res: HttpResponse<any>) => this.onDeleteSuccess(res), (res: HttpErrorResponse) => this.onDeleteError(res));
    }

    deleteOrganisation(organisation) {
        var localthisObject = this;
        swal({
            title: "Are you sure to delete this organisation ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: false,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localthisObject.confirmDelete(organisation.id);
                } else {
                    localthisObject.previousUrl();
                }
            });
    }

    confirmDelete(id: number) {
        this.subscribeToDeleteResponse(this.organisationService.deleteOrganisation(id));
    }

    previousUrl() {
        this.router.navigateByUrl('/organisation');
    }

    protected onDeleteSuccess(resp) {
        this.pluginService.alertMessage("Organisation deleted successfully", "success");
        this.previousUrl();
    }

    onDeleteError(err: HttpErrorResponse) {
        this.pluginService.alertMessage("Could not delete organisation, please contact admin", "error")
    }
}