import { Component } from '@angular/core';
import { IOrganisationSubSchool } from '../shared/model/common/organisation-sub-school.model';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder } from '@angular/forms';
import { PluginService } from '../shared/plugin/plugin.service';
import { OrganisationSubSchoolService } from './organisation-sub-school.service';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';


declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./organisation-sub-school-details.component.html",
    providers: [OrganisationSubSchoolService]
})
export class OrganisationSubSchoolDetailsComponent {
    organisationSubSchool: IOrganisationSubSchool = {};

    constructor(
        public _router: Router,
        public activatedRoute: ActivatedRoute,
        public formBuilder: FormBuilder,
        public pluginService: PluginService,
        public organisationService: OrganisationSubSchoolService
    ) {
    }

    ngOnInit() {
        this.pluginService.newStepperJs();
        this.activatedRoute.params.subscribe(params => {
            let organisationSchoolId = params['schoolId'];
            this.getOrganisationSubSchoolDetails(organisationSchoolId);
        });
    }

    getOrganisationSubSchoolDetails(schoolId: number) {
        this.organisationService
            .getOrganisationSchoolBySchoolId(schoolId)
            .subscribe(
                (res: HttpResponse<IOrganisationSubSchool>) => this.assignOrganisationSubSchool(res.body),
                (res: HttpErrorResponse) => this.onError(res.message)
            );
    }

    assignOrganisationSubSchool(data: IOrganisationSubSchool) {
        this.organisationSubSchool = data;
    }

    onError(res) {
        this.pluginService.alertMessage("Could not get organisation subsidairy school details contact admin", "error");
    }

    onBack() {
        var localObj = this;
        swal({
            title: "Are you sure to go previous page ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: true,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localObj._router.navigate(['/organisation', localObj.organisationSubSchool.parentOrganisationId, 'school']);
                }
            });
    }
}