import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { JhiEventManager } from 'ng-jhipster';
import { OrganisationSubSchoolService } from './organisation-sub-school.service';
import { IOrganisationSubSchool } from '../shared/model/common/organisation-sub-school.model';

declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./organisation-sub-school-manage.component.html",
    providers: [OrganisationSubSchoolService]
})
export class OrganisationSubSchoolDeleteComponent implements OnInit {
    organisationId: number;

    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        protected eventManager: JhiEventManager,
        public organisationService: OrganisationSubSchoolService,
        protected pluginService: PluginService
    ) { }

    ngOnInit() {
        this.activatedRoute.params.subscribe(params => {
            let organisationSchoolId = params['schoolId'];
            this.getOrganisationSubSchoolDetails(organisationSchoolId);
        });
    }

    getOrganisationSubSchoolDetails(schoolId: number) {
        this.organisationService
            .getOrganisationSchoolBySchoolId(schoolId)
            .subscribe(
                (res: HttpResponse<IOrganisationSubSchool>) => this.assignOrganisationSubSchool(res.body),
                (res: HttpErrorResponse) => this.onError(res.message)
            );
    }

    assignOrganisationSubSchool(data: IOrganisationSubSchool) {
        let organisationSubSchool = data;
        this.organisationId = organisationSubSchool.parentOrganisationId;
        this.deleteOrganisationSubSchool(organisationSubSchool);
    }

    onError(res) {
        this.pluginService.alertMessage("Could not get organisation subsidairy school details contact admin", "error");
    }

    protected subscribeToDeleteResponse(resp: Observable<HttpResponse<any>>) {
        resp.subscribe((res: HttpResponse<any>) => this.onDeleteSuccess(res), (res: HttpErrorResponse) => this.onDeleteError(res));
    }

    deleteOrganisationSubSchool(organisationSubSchool) {
        var localthisObject = this;
        swal({
            title: "Are you sure to delete this organisation subsidiary school?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: false,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localthisObject.confirmDelete(organisationSubSchool.id);
                } else {
                    localthisObject.previousUrl();
                }
            });
    }

    confirmDelete(id: number) {
        this.subscribeToDeleteResponse(this.organisationService.deleteOrgSchool(id));
    }

    previousUrl() {
        this.router.navigate(['/organisation', this.organisationId, 'school']);
    }

    protected onDeleteSuccess(resp) {
        this.pluginService.alertMessage("Organisation subsidiary school deleted successfully", "success");
        this.previousUrl();
    }

    onDeleteError(err: HttpErrorResponse) {
        this.pluginService.alertMessage("Could not delete organisation subsidiary school, please contact admin", "error")
    }
}