import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { JhiEventManager } from 'ng-jhipster';
import { OrganisationSchoolUserService } from './organisation-school-user.service';

declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./organisation-school-user-manage.component.html",
    providers: [OrganisationSchoolUserService]
})
export class OrganisationSchoolUserDeleteComponent implements OnInit {

    organisationId: number;
    schoolId: number;
    login: string;

    constructor(
        public router: Router,
        public activatedRoute: ActivatedRoute,
        protected eventManager: JhiEventManager,
        public organisationService: OrganisationSchoolUserService,
        protected pluginService: PluginService
    ) { }

    ngOnInit() {
        this.activatedRoute.params.subscribe(params => {
            this.organisationId = params['id'];
            this.schoolId = params['schoolId'];
            this.login = params['login'];
            this.deleteOrganisationSchoolAdminUser();
        });
    }

    protected subscribeToDeleteResponse(resp: Observable<HttpResponse<any>>) {
        resp.subscribe((res: HttpResponse<any>) => this.onDeleteSuccess(res), (res: HttpErrorResponse) => this.onDeleteError(res));
    }

    deleteOrganisationSchoolAdminUser() {
        var localthisObject = this;
        swal({
            title: "Are you sure to delete this organisation school admin user ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: false,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localthisObject.confirmDelete(localthisObject.login);
                } else {
                    localthisObject.previousUrl();
                }
            });
    }

    confirmDelete(login: string) {
        this.subscribeToDeleteResponse(this.organisationService.deleteSchoolUser(login));
    }

    previousUrl() {
        this.router.navigate(['/organisation', this.organisationId, 'school', this.schoolId, 'school-user']);
    }

    protected onDeleteSuccess(resp) {
        this.pluginService.alertMessage("Organisation school admin user deleted successfully", "success");
        this.previousUrl();
    }

    onDeleteError(err: HttpErrorResponse) {
        this.pluginService.alertMessage("Could not delete organisation school admin user, please contact admin", "error")
    }

}