import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { IOrganisationSubSchool } from '../shared/model/common/organisation-sub-school.model';

type OrgSchoolResponseType = HttpResponse<IOrganisationSubSchool>;
type OrgSchoolArrayResponseType = HttpResponse<IOrganisationSubSchool[]>;

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', '*').append('Access-Control-Allow-Headers', '*');

@Injectable({ providedIn: 'root' })
export class OrganisationSubSchoolService {

    public schoolResourceUrl = environment.API_URL + 'services/common/api/schools';
    public schoolSearchResourceUrl = environment.API_URL + 'services/common/api/schools/search';

    constructor(protected http: HttpClient) { }

    getSchoolByOrgId(organisationId?: number): Observable<OrgSchoolArrayResponseType> {
        // const options = createRequestOption(req);
        var reqJSON = {
            "parentOrganisationId": organisationId
        };
        return this.http.post<IOrganisationSubSchool[]>(this.schoolSearchResourceUrl, reqJSON, { observe: 'response' });
    }

    createOrgSchool(orgSchool: IOrganisationSubSchool): Observable<OrgSchoolResponseType> {
        return this.http.post<IOrganisationSubSchool>(this.schoolResourceUrl, orgSchool, { headers: headers, observe: 'response' });
    }

    updateOrgSchool(organisation: IOrganisationSubSchool): Observable<OrgSchoolResponseType> {
        return this.http.put<IOrganisationSubSchool>(this.schoolResourceUrl, organisation, { observe: 'response' });
    }

    getOrganisationSchoolBySchoolId(id: number): Observable<OrgSchoolResponseType> {
        return this.http.get<IOrganisationSubSchool>(`${this.schoolResourceUrl}/${id}`, { observe: 'response' });
    }

    deleteOrgSchool(id: number): Observable<HttpResponse<any>> {
        return this.http.delete<any>(`${this.schoolResourceUrl}/${id}`, { observe: 'response' });
    }
}
