import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { IOrganisationUsers } from '../shared/model/common/organisation-users.model';
import { IUsers } from '../shared/model/common/users.model';

type OrgUserResponseType = HttpResponse<IOrganisationUsers>;
type OrgUserArrayResponseType = HttpResponse<IOrganisationUsers[]>;
type userResponseType = HttpResponse<IUsers>;

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', '*').append('Access-Control-Allow-Headers', '*');

@Injectable({ providedIn: 'root' })
export class OrganisationSchoolUserService {

    public orgUsersResourceUrl = environment.API_URL + 'services/common/api/app-users';
    public orgUsersSearchResourceUrl = environment.API_URL + 'services/common/api/app-users/basic-search';
    public usersResourceUrl = environment.API_URL + 'api/users';
    public usersSearchResourceUrl = environment.API_URL + 'api/users/search';

    constructor(protected http: HttpClient) { }

    getSchoolUserBySchoolId(organisationId?: number, schoolId?: number): Observable<OrgUserArrayResponseType> {
        // const options = createRequestOption(req);
        var reqJSON = {
            "organisationId": organisationId,
            "schoolId": schoolId
        };
        return this.http.post<IOrganisationUsers[]>(this.orgUsersSearchResourceUrl, reqJSON, { observe: 'response' });
    }

    getUserDetailByUserId(userId?: number): Observable<userResponseType> {
        var reqJSON = {
            "id": userId
        };
        return this.http.post<IUsers>(this.usersSearchResourceUrl, reqJSON, { observe: 'response' });
    }

    createSchoolUser(orgSchool: IOrganisationUsers): Observable<OrgUserResponseType> {
        return this.http.post<IOrganisationUsers>(this.orgUsersResourceUrl, orgSchool, { headers: headers, observe: 'response' });
    }

    updateSchoolUser(organisationUser: IUsers): Observable<userResponseType> {
        return this.http.put<IUsers>(this.usersResourceUrl, organisationUser, { observe: 'response' });
    }

    deleteSchoolUser(login?: string): Observable<HttpResponse<any>> {
        return this.http.delete<any>(`${this.usersResourceUrl}/${login}`, { observe: 'response' });
    }
}
