import { Component, OnInit, OnDestroy } from "@angular/core";
import { Router, ActivatedRoute } from '@angular/router';
//import { JhiEventManager, JhiParseLinks, JhiAlertService } from 'ng-jhipster';
import { JhiEventManager, JhiParseLinks } from 'ng-jhipster';
import { Subscription } from 'rxjs';
import { Principal } from '../core';
import { ITEMS_PER_PAGE } from '../shared';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../shared/plugin/plugin.service';
import { OrganisationUserService } from './organisation-user.service';
import { IOrganisationUsers } from '../shared/model/common/organisation-users.model';

declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./organisation-user-manage.component.html",
    providers: [OrganisationUserService]
})
export class OrganisationUserManageComponent implements OnInit, OnDestroy {
    organisationAdminUserList: IOrganisationUsers[];
    currentAccount: any;
    eventSubscriber: Subscription;
    itemsPerPage: number;
    page: any;
    predicate: any;
    reverse: any;
    totalItems: number;
    totalPages: number;
    links: any;
    organisationId: number;

    constructor(
        public _router: Router,
        public activatedRoute: ActivatedRoute,
        public organisationSubSchoolService: OrganisationUserService,
        protected eventManager: JhiEventManager,
        protected parseLinks: JhiParseLinks,
        protected principalService: Principal,
        public pluginService: PluginService
    ) {
        this.organisationAdminUserList = [];
        this.itemsPerPage = ITEMS_PER_PAGE;
        this.page = 0;
        this.links = {
            last: 0
        };
        this.predicate = 'id';
        // descending
        this.reverse = false;
    }

    ngOnInit() {
        this.pluginService.sideMenuToggle();
        this.principalService.identity().then(account => {
            this.currentAccount = account;
        });
        this.registerChangeInOrganisationUserAdmin();
        this.activatedRoute.data.subscribe(({ organisation }) => {
            this.organisationId = organisation.id;
            this.getOrganisationUserByOrganisationId();
        });
    }

    getOrganisationUserByOrganisationId() {
        this.organisationSubSchoolService
            .getOrganisationUserByOrgUserId(this.organisationId)
            .subscribe(
                (res: HttpResponse<IOrganisationUsers[]>) => this.assignOrgUserList(res.body),
                (res: HttpErrorResponse) => this.onError(res.message)
            );
    }

    protected assignOrgUserList(data: IOrganisationUsers[]) {
        this.organisationAdminUserList = data;
    }

    protected onError(errorMessage: string) {
        this.pluginService.alertMessage(errorMessage, "error");
    }

    reset() {
        this.page = 0;
        this.organisationAdminUserList = [];
        this.getOrganisationUserByOrganisationId();
    }

    loadPage(page) {
        this.page = page;
        this.getOrganisationUserByOrganisationId();
    }


    ngOnDestroy() {
        this.eventManager.destroy(this.eventSubscriber);
    }

    trackId(index: number, item: IOrganisationUsers) {
        return item.organisationId;
    }

    registerChangeInOrganisationUserAdmin() {
        this.eventSubscriber = this.eventManager.subscribe('organisationUserListModification', response => this.reset());
    }

    sort() {
        const result = [this.predicate + ',' + (this.reverse ? 'asc' : 'desc')];
        if (this.predicate !== 'id') {
            result.push('id');
        }
        return result;
    }


    ngDestroy() {
        this.eventSubscriber.unsubscribe();
    }

    onBack() {
        var localObj = this;
        swal({
            title: "Are you sure to go previous page ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: true,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localObj._router.navigateByUrl('/organisation');
                    localObj.pluginService.sideMenuToggle();
                }
            });
    }
}





