import { Component, OnInit, OnDestroy } from "@angular/core";
import { Router, ActivatedRoute } from '@angular/router';
import { JhiEventManager, JhiParseLinks } from 'ng-jhipster';
import { Subscription } from 'rxjs';
import { Principal } from '../core';
import { ITEMS_PER_PAGE } from '../shared';
import { HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { OrganisationService } from './organisation.service';
import { IOrganisation } from '../shared/model/common/organisation.model';
import { PluginService } from '../shared/plugin/plugin.service';

declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./organisation-manage.component.html",
    providers: [OrganisationService]
})
export class OrganisationManageComponent implements OnInit, OnDestroy {
    organisations: IOrganisation[];
    currentAccount: any;
    eventSubscriber: Subscription;
    itemsPerPage: number;
    page: any;
    predicate: any;
    reverse: any;
    totalItems: number;
    totalPages: number;
    links: any;

    constructor(
        public _router: Router,
        public activatedRoute: ActivatedRoute,
        public organisationService: OrganisationService,
        protected eventManager: JhiEventManager,
        protected parseLinks: JhiParseLinks,
        protected principalService: Principal,
        public pluginService: PluginService
    ) {
        this.organisations = [];
        this.itemsPerPage = ITEMS_PER_PAGE;
        this.page = 0;
        this.links = {
            last: 0
        };
        this.predicate = 'id';
        this.reverse = false;
    }

    loadOrganisationList() {
        this.organisationService
            .getOrganisations({
                page: this.page,
                size: this.itemsPerPage,
                sort: this.sort()
            })
            .subscribe(
                (res: HttpResponse<IOrganisation[]>) => this.paginateOrganisations(res.body, res.headers),
                (res: HttpErrorResponse) => this.onError(res.message)
            );
    }

    protected paginateOrganisations(data: IOrganisation[], headers: HttpHeaders) {
        this.links = this.parseLinks.parse(headers.get('link'));
        this.totalItems = parseInt(headers.get('X-Total-Count'), 10);
        if (data.length > 0) {
            for (let i = 0; i < data.length; i++) {
                this.organisations.push(data[i]);
            }
        } else {
            this.pluginService.alertMessage("No data found..!", "warning");
        }
    }

    protected onError(errorMessage: string) {
        this.pluginService.alertMessage(errorMessage, "error");
    }

    reset() {
        this.page = 0;
        this.organisations = [];
        this.loadOrganisationList();
    }

    loadPage(page) {
        this.page = page;
        this.loadOrganisationList();
    }

    ngOnInit() {
        this.loadOrganisationList();
        this.principalService.identity().then(account => {
            this.currentAccount = account;
        });
        this.registerChangeInOrganisations();
    }

    ngOnDestroy() {
        this.eventManager.destroy(this.eventSubscriber);
    }

    trackId(index: number, item: IOrganisation) {
        return item.id;
    }

    registerChangeInOrganisations() {
        this.eventSubscriber = this.eventManager.subscribe('organisationListModification', response => this.reset());
    }

    sort() {
        const result = [this.predicate + ',' + (this.reverse ? 'asc' : 'desc')];
        if (this.predicate !== 'id') {
            result.push('id');
        }
        return result;
    }

    ngDestroy() {
        this.eventSubscriber.unsubscribe();
    }
}





