import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder } from '@angular/forms';
import { PluginService } from '../shared/plugin/plugin.service';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { OrganisationUserService } from './organisation-user.service';
import { IUsers } from '../shared/model/common/users.model';
import { formatDate } from '@angular/common';


declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./organisation-user-details.component.html",
    providers: [OrganisationUserService]
})
export class OrganisationUserDetailsComponent implements OnInit {
    organisationAdminUser: IUsers = {};
    organisationId: number;
    organisationUserId: number;
    authorities: any[] = [];
    authorityNames: string;
    dateOfBirth: string;
    joiningDate: string;

    constructor(
        public _router: Router,
        public activatedRoute: ActivatedRoute,
        public formBuilder: FormBuilder,
        public pluginService: PluginService,
        public organisationService: OrganisationUserService
    ) {
        this.organisationAdminUser = {};
        this.authorities = [
            {
                id: "orgAdmin",
                role: "ORG_USER_ADMIN",
                name: "Organisation admin",
                checked: false
            },
            {
                id: "contentAdmin",
                role: "ORG_CONTENT_ADMIN",
                name: "Content admin",
                checked: false
            }
        ]
    }

    ngOnInit() {
        this.activatedRoute.params.subscribe(params => {
            this.organisationId = params['id'];
            this.organisationUserId = params['orgUserId'];
            this.getOrganisationUserDetails(this.organisationUserId);
        });
    }

    getOrganisationUserDetails(userId: number) {
        this.organisationService
            .getUserDetailByUserId(userId)
            .subscribe(
                (res: HttpResponse<IUsers>) => this.assignOrganisationUser(res.body),
                (res: HttpErrorResponse) => this.onError(res.message)
            );
    }

    assignOrganisationUser(data: IUsers) {
        let organisationAdminUser = data[0];
        this.patchOrganisationAdminUserForm(organisationAdminUser);
    }

    public convertDateFormat(dateValue): string {
        let dateFormat = formatDate(dateValue, 'dd-MM-yyyy', 'en-us');
        return dateFormat;
    }

    patchOrganisationAdminUserForm(organisationAdminUser) {

        let authorities: any[] = [], selectedAuthorities: any[] = [];

        authorities = organisationAdminUser.authorities;
        this.dateOfBirth = this.convertDateFormat(organisationAdminUser.dateOfBirth);
        this.joiningDate = this.convertDateFormat(organisationAdminUser.joiningDate);
        for (let i = 0; i < this.authorities.length; i++) {
            for (let j = 0; j < authorities.length; j++) {
                if (this.authorities[i].role === authorities[j]) {
                    this.authorities[i].checked = true;
                    selectedAuthorities.push(this.authorities[i].name);
                }
            }
        }
        this.authorityNames = selectedAuthorities.toString();
        this.organisationAdminUser = organisationAdminUser;
    }

    onError(res) {
        this.pluginService.alertMessage("Could not get organisation user details contact admin", "error");
    }

    previousPage() {
        this._router.navigate(['/organisation', this.organisationId, 'organisation-user']);
    }

    onBack() {
        var localObj = this;
        swal({
            title: "Are you sure to go previous page ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: true,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localObj.previousPage();
                }
            });
    }
}