import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { createRequestOption } from '../shared';
import { IOrganisation } from '../shared/model/common/organisation.model';

type EntityResponseType = HttpResponse<IOrganisation>;
type EntityArrayResponseType = HttpResponse<IOrganisation[]>;

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', '*').append('Access-Control-Allow-Headers', '*');

@Injectable({ providedIn: 'root' })
export class OrganisationService {

    public organisationResourceUrl = environment.API_URL + 'services/common/api/organisations';
    public curriculumResourceUrlSearch = environment.API_URL + 'services/learning/api/curricula/search';

    constructor(protected http: HttpClient) { }

    createOrganisation(organisation: IOrganisation): Observable<EntityResponseType> {
        return this.http.post<IOrganisation>(this.organisationResourceUrl, organisation, { headers: headers, observe: 'response' });
    }

    updateOrganisation(organisation: IOrganisation): Observable<EntityResponseType> {
        return this.http.put<IOrganisation>(this.organisationResourceUrl, organisation, { observe: 'response' });
    }

    getOrganisationByOrganisationId(id: number): Observable<EntityResponseType> {
        return this.http.get<IOrganisation>(`${this.organisationResourceUrl}/${id}`, { observe: 'response' });
    }

    getOrganisations(req?: any): Observable<EntityArrayResponseType> {
        const options = createRequestOption(req);
        return this.http.get<IOrganisation[]>(this.organisationResourceUrl, { params: options, observe: 'response' });
    }

    deleteOrganisation(id: number): Observable<HttpResponse<any>> {
        return this.http.delete<any>(`${this.organisationResourceUrl}/${id}`, { observe: 'response' });
    }
}
