import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder } from '@angular/forms';
import { PluginService } from '../shared/plugin/plugin.service';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { IUsers } from '../shared/model/common/users.model';
import { OrganisationSchoolUserService } from './organisation-school-user.service';
import { formatDate } from '@angular/common';


declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./organisation-school-user-details.component.html",
    providers: [OrganisationSchoolUserService]
})
export class OrganisationSchoolUserDetailsComponent implements OnInit {

    organisationSchoolAdminUser: IUsers = {};
    organisationId: number;
    schoolId: number;
    schoolUserId: number;
    authorities: any[] = [];
    authorityNames: string;
    dateOfBirth: string;
    joiningDate: string;

    constructor(
        public _router: Router,
        public activatedRoute: ActivatedRoute,
        public formBuilder: FormBuilder,
        public pluginService: PluginService,
        public organisationService: OrganisationSchoolUserService
    ) {

        this.organisationSchoolAdminUser = {};
        this.authorities = [
            {
                id: "schoolAdmin",
                role: "SCHOOL_ADMIN",
                name: "School admin",
                checked: false
            },
            {
                id: "principal",
                role: "PRINCIPAL",
                name: "Principal",
                checked: false
            }
        ]
    }

    ngOnInit() {
        this.activatedRoute.params.subscribe(params => {
            this.organisationId = params['id'];
            this.schoolId = params['schoolId'];
            this.schoolUserId = params['schoolUserId'];
            this.getOrganisationUserDetails();
        });
    }

    getOrganisationUserDetails() {
        this.organisationService
            .getUserDetailByUserId(this.schoolUserId)
            .subscribe(
                (res: HttpResponse<IUsers>) => this.assignOrganisationSchoolAdminUser(res.body),
                (res: HttpErrorResponse) => this.onError(res.message)
            );
    }

    assignOrganisationSchoolAdminUser(data: IUsers) {
        let organisationSchoolAdminUser = data[0];
        this.patchOrganisationSchoolAdminUserForm(organisationSchoolAdminUser);

    }

    public convertDateFormat(dateValue): string {
        let dateFormat = formatDate(dateValue, 'dd-MM-yyyy', 'en-us');
        return dateFormat;
    }

    patchOrganisationSchoolAdminUserForm(organisationSchoolAdminUser) {
        let authorities: any[] = [], selectedAuthorities: any[] = [];

        this.dateOfBirth = this.convertDateFormat(organisationSchoolAdminUser.dateOfBirth);
        this.joiningDate = this.convertDateFormat(organisationSchoolAdminUser.joiningDate);
        authorities = organisationSchoolAdminUser.authorities;
        for (let i = 0; i < this.authorities.length; i++) {
            for (let j = 0; j < authorities.length; j++) {
                if (this.authorities[i].role === authorities[j]) {
                    this.authorities[i].checked = true;
                    selectedAuthorities.push(this.authorities[i].name);
                }
            }
        }
        this.authorityNames = selectedAuthorities.toString();
        this.organisationSchoolAdminUser = organisationSchoolAdminUser;
    }

    onError(res) {
        this.pluginService.alertMessage("Could not get organisation school admin user details contact admin", "error");
    }

    previousPage() {
        this._router.navigate(['/organisation', this.organisationId, 'school', this.schoolId, 'school-user']);
    }

    onBack() {
        var localObj = this;
        swal({
            title: "Are you sure to go previous page ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: true,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localObj.previousPage();
                }
            });
    }
}