import { Component, OnInit, OnDestroy } from "@angular/core";
import { Router, ActivatedRoute } from '@angular/router';
//import { JhiEventManager, JhiParseLinks, JhiAlertService } from 'ng-jhipster';
import { JhiEventManager, JhiParseLinks } from 'ng-jhipster';
import { Subscription } from 'rxjs';
import { Principal } from '../core';
import { ITEMS_PER_PAGE } from '../shared';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { IOrganisationSubSchool } from '../shared/model/common/organisation-sub-school.model';
import { PluginService } from '../shared/plugin/plugin.service';
import { OrganisationSubSchoolService } from './organisation-sub-school.service';

declare var $: any;
declare var swal: any;

@Component({
    templateUrl: "./organisation-sub-school-manage.component.html",
    providers: [OrganisationSubSchoolService]
})
export class OrganisationSubSchoolManageComponent implements OnInit, OnDestroy {
    schoolList: IOrganisationSubSchool[];
    eventSubscriber: Subscription;
    itemsPerPage: number;
    totalItems: number;
    totalPages: number;
    organisationId: number;
    currentAccount: any;
    predicate: any;
    reverse: any;
    links: any;
    page: any;

    constructor(
        public _router: Router,
        public activatedRoute: ActivatedRoute,
        public organisationSubSchoolService: OrganisationSubSchoolService,
        protected eventManager: JhiEventManager,
        protected parseLinks: JhiParseLinks,
        protected principalService: Principal,
        public pluginService: PluginService
    ) {
        this.schoolList = [];
        this.itemsPerPage = ITEMS_PER_PAGE;
        this.page = 0;
        this.links = {
            last: 0
        };
        this.predicate = 'id';
        this.reverse = false;
    }

    ngOnInit() {
        this.pluginService.sideMenuToggle();
        this.principalService.identity().then(account => {
            this.currentAccount = account;
        });
        this.registerChangeInOrganisationSchool();
        this.activatedRoute.data.subscribe(({ organisation }) => {
            this.organisationId = organisation.id;
            this.getSchoolListByOrgID();
        });
    }

    getSchoolListByOrgID() {
        this.organisationSubSchoolService
            .getSchoolByOrgId(this.organisationId)
            .subscribe(
                (res: HttpResponse<IOrganisationSubSchool[]>) => this.assignOrgSchoolList(res.body),
                (res: HttpErrorResponse) => this.onError(res.message)
            );
    }

    protected assignOrgSchoolList(data: IOrganisationSubSchool[]) {
        if (data.length > 0) {
            this.schoolList = data;
        } else {
            this.pluginService.alertMessage("No data found..!", "warning");
        }
    }

    protected onError(errorMessage: string) {
        this.pluginService.alertMessage(errorMessage, "error");
    }

    reset() {
        this.page = 0;
        this.schoolList = [];
        this.getSchoolListByOrgID();
    }

    loadPage(page) {
        this.page = page;
        this.getSchoolListByOrgID();
    }

    ngOnDestroy() {
        this.eventManager.destroy(this.eventSubscriber);
    }

    trackId(index: number, item: IOrganisationSubSchool) {
        return item.id;
    }

    registerChangeInOrganisationSchool() {
        this.eventSubscriber = this.eventManager.subscribe('organisationSubSchoolListModification', response => this.reset());
    }

    sort() {
        const result = [this.predicate + ',' + (this.reverse ? 'asc' : 'desc')];
        if (this.predicate !== 'id') {
            result.push('id');
        }
        return result;
    }

    onBack() {
        var localObj = this;
        swal({
            title: "Are you sure to go previous page ?",
            type: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            confirmButtonColor: "#32b432",
            cancelButtonText: "No",
            closeOnConfirm: true,
            closeOnCancel: true
        },
            function (isConfirm) {
                if (isConfirm) {
                    localObj._router.navigateByUrl('/organisation');
                    localObj.pluginService.sideMenuToggle();
                }
            });
    }
}





